.store-hr {
  background-color: #c7cf43;
  height:1px;
  border: none;
}

.spec-wrap {
  width: 1000px;
  margin: 50px auto 0 auto;
  &::after {
    content: "";
    display: block;
    clear: both;
  }
}
.spec-smail-picture-wrap {
  width: 420px;
  box-sizing: border-box;
  float: left;
  vertical-align: middle;
  text-align: center;
}
.spec-smail-bigone-wrap {
  height: 430px;
  overflow: hidden;
}
.spec-smail-bigone {
  cursor: pointer;
}
.spec-smaill-item-wrap {
  padding: 20px 0 10px 0;
  &::after {
    content: "";
    display: block;
    clear: both;
  }
  .spec-priture-item {
    width: 20%;
    box-sizing: border-box;
    padding: 0 5px;
    float: left;
    font-size: 0;
    cursor: pointer;
  }
  .spec-item-img-box {
    border: 2px solid transparent;
  }
  .spec-img-selected {
    border: 2px solid black;
  }
}

.spec-goodinfo {
  width: 580px;
  box-sizing: border-box;
  padding: 5px 30px;
  float: left;
  .spec-good-name {
    text-align: left;
    font-size: 17px;
    font-weight: bold;
  }
  .spec-good-price {
    padding: 5px 20px;
    margin-top: 15px;
    .sgp-f1 {
      font-size: 10px;
      color: #bbb;
      margin-right: 12px;
    }
    .sgp-f2 {
      font-size: 15px;
      font-weight: bold;
      color: #f40;
    }
    .sgp-f3 {
      color: #f40;
      font-weight: bold;
      font-size: 30px;
    }
  }
}
.sell-count {
  border-top: 1px dotted #ccc;
  border-bottom: 1px dotted #ccc;
  padding: 5px 0;
  margin: 15px 0;
  text-align: center;
  font-size: 12px;
  &::after {
    content: "";
    display: block;
    clear: both;
  }
  .sell-count-l,.sell-count-r {
    float: left;
    width: 50%;
    box-sizing: border-box;
  }
  .sell-count-l {
    border-right: 1px dotted #ccc;
  }
  .sc-f1 {
    font-size: 10px;
    color: #bbb;
    margin-right: 4px;
  }
  .sc-f2 {
    color: #f40;
    font-size: 10px;
    font-weight: bold;
  }
}
.specName-wrap {
  font-size: 12px;
  line-height: 28px;
  margin-bottom: 5px;
  &::after {
    content: "";
    display: block;
    clear: both;
  }
  .spec-name {
    float: left;
    padding: 0 20px;
    color: #838383;
  }
  .spec-value {
    float: left;
    &::after {
    content: "";
    display: block;
    clear: both;
    color: #000;
  }
  }
  .spec-value-item {
    float: left;
    box-sizing: border-box;
    border: 1px solid #666;
    padding: 1px 9px;
    margin-bottom: 5px;
    margin-right: 5px;
    cursor: pointer;
    position: relative;
    &:hover {
      border: 2px solid #f40;
      padding: 0px 8px;
    }
  }
  .item-selected-i {
    width: 12px;
    height: 12px;
    position: absolute;
    right: 0;
    bottom: 0;
    display: none;
  }
  .value-item-selected {
    border: 2px solid #f40;
    padding: 0px 8px;

    .item-selected-i {
      display: block;
    }
  }
}
